import React from 'react';
import whitelogo from '../src/images/white-logo.png';
import { NavLink } from 'react-router-dom';

const Navbar = () => {

    /* Open when someone clicks on the span element */
    const openNav = () => {
        document.getElementById("myNav").style.width = "100%";
    }

    /* Close when someone clicks on the "x" symbol inside the overlay */
    const closeNav = () => {
        document.getElementById("myNav").style.width = "0%";
    }

    return (
        <>
            <div id="navbar" className="nav-bg">
                <div className="container">
                    <nav className="flex-setting p-2">
                        <NavLink to="/" className="navbar-brand"><img src={whitelogo} alt=""/></NavLink>                 <a onClick={openNav} className="btn btn-link" id="menu-btn">
                            <span>Menu</span>
                            <i className="fa fa-bars" style={{ fontSize: '18px', color: 'white' }}></i>
                        </a>
                    </nav>
                </div>

                {/*  The overlay  */}
                <div id="myNav" className="overlay">

                    {/*  Button to close the overlay navigation  */}
                    <a href="javascript:void(0)" className="closebtn" onClick={closeNav}>&times;</a>

                    {/* Overlay content */}
                    <div className="overlay-content">
                        <ul className="links">
                            <li>
                                <NavLink exact to="/portfolio">Portfolio</NavLink>
                                <div id="portfolio"></div>
                            </li>
                            <li>
                                <NavLink exact to="/contact">Contact</NavLink>
                                <div id="contact"></div>
                            </li>
                        </ul>
                    </div>

                </div>

            </div>
        </>
    );
}

export default Navbar;