import React from 'react';
import { NavLink } from 'react-router-dom';
import Typical from 'react-typical';
import Portfolio from './Portfolio';
import Contact from './Contact';
import cv from './images/cv_pdf.pdf';

const Home = () => {
    
    return (
        <>
            <section id="home">
                <div className="wrapper">
                    <div className="bg-img" id="app">
                        <div className="center-content" id="wrapper">
                            {/* <h1 className="name border">Hi, I am Wasiq<br/><span className="tag-line">Front-end Designer</span></h1> */}
                            <h1 className="j glitch" data-text="Let's make things">Let's make things
                            <Typical 
                            loop={Infinity}
                            wrapper="b"
                            steps={[
                                ' Simple',3000,
                                ' Magical',3000,
                                ' Meaningful',3000,
                                ' Inspiring',3000,
                                ' Impactful',3000,
                                ' Powerful',3000,
                            ]}
                            />
                            .</h1>
                        </div>
                    </div>

                    <div id="about">
                        <div className="maxwidth">
                            <h1>About</h1>
                            <hr/>
                            <div className="content">
                                <p className="large">Hi, I’m Khan. I'm a multi-talented human with over years of experiences in wide range of design disciplines.</p>
                                <p>You can also call me a product designer, experience designer, interaction, UI, UX or by any other market defined function-title. I prefer to keep learning, continue challenging myself, and do interesting things that matter. My abundant energy fuels me in the pursuit of many interests, hobbies, areas of study and artistic endeavors. I’m a fast learner, able to pick up new skills and juggle different projects and roles with relative ease.</p>
                                <p className="resume_para">
                                <a className="resume" href={cv} target="_blank">View Resume</a>
                                </p>
                            </div>
                            
                            <div className="sub-nav">
                                <h6 className="next">Next</h6>
                                <ul className="sub-nav-links">
                                    <li>
                                        <NavLink exact to="/portfolio">Portfolio</NavLink>
                                    </li>
                                    <li>
                                        <NavLink exact to="/contact">Contact</NavLink>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Home;