import React from 'react';
import techautomation from '../src/images/tech-automation.png';
import startersite2 from '../src/images/starter-site2.png';
import foodrecipe from '../src/images/foodrecipe.png';
import startersite from '../src/images/starter-site.png';
import todolist from '../src/images/todo-list.png';
import khaleejships from '../src/images/khaleej-ships.png';

import { NavLink } from 'react-router-dom';

const Portfolio = () => {
    return (
        <>
            <section id="Portfolio">
                <div className="maxwidth">
                    <h1 className="my-5">Portfolio</h1>
                </div>

                <div class="maxwidth" id="accordionExample">

                    <div class="">
                        <div class="card-header maxwidth" id="headingOne">
                            <h2 class="mb-0">
                                <button class="btn btn-link btn-block text-left collapsed proj-btn" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="false" aria-controls="collapseTwo">
                                    Tech Automation </button><hr />
                            </h2>
                            <div className="img-center">
                                <div id="hover-content">
                                    <img id="proj-img" src={techautomation} className="img-fluid" alt="tech-automation" />
                                </div>
                            </div>
                        </div>
                        <div id="collapseOne" class="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
                            <div class="card-body">
                                <p>A live website made for our client who runs TECH Automation and Controls Systems mainly deals in Building Management System.</p>
                                <a href="http://tech-automation.net/" target="_blank">View</a>
                            </div>
                        </div>
                    </div>

                    <div class="">
                        <div class="card-header" id="headingTwo">
                            <h2 class="mb-0">
                                <button class="btn btn-link btn-block text-left collapsed proj-btn" type="button" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseThree">
                                    Starter Website for Business
                                </button><hr />
                                <div id="hover-content">
                                    <img id="proj-img" src={startersite2} className="img-fluid" alt="tech-automation" />
                                </div>
                            </h2>
                        </div>
                        <div id="collapseTwo" class="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">
                            <div class="card-body">
                                <p>Take a look at beautiful and complete starter website for any business.</p>
                                <a href="https://waris-khan.github.io/starter-site2/" target="_blank">View</a>
                            </div>
                        </div>
                    </div>

                    <div class="">
                        <div class="card-header" id="headingFive">
                            <h2 class="mb-0">
                                <button class="btn btn-link btn-block text-left collapsed proj-btn" type="button" data-toggle="collapse" data-target="#collapseFive" aria-expanded="false" aria-controls="collapseThree">
                                    Food Recipe App
                                </button><hr />
                                <div id="hover-content">
                                    <img id="proj-img" src={foodrecipe} className="img-fluid" alt="food-recipe" />
                                </div>
                            </h2>
                        </div>
                        <div id="collapseFive" class="collapse" aria-labelledby="headingFive" data-parent="#accordionExample">
                            <div class="card-body">
                                <p>Find your favourite recipe and cook it.</p>
                                <a href="https://waris-khan.github.io/food-recipe/" target="_blank">View</a>
                            </div>
                        </div>
                    </div>

                    <div class="">
                        <div class="card-header" id="headingThree">
                            <h2 class="mb-0">
                                <button class="btn btn-link btn-block text-left collapsed proj-btn" type="button" data-toggle="collapse" data-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                                    Business Website for Startup
                                </button><hr />
                                <div id="hover-content">
                                    <img id="proj-img" src={startersite} className="img-fluid" alt="startersite" />
                                </div>
                            </h2>
                        </div>
                        <div id="collapseThree" class="collapse" aria-labelledby="headingThree" data-parent="#accordionExample">
                            <div class="card-body">
                                <p>A complete and minimal website for starting your any startup.</p>
                                <a href="https://waris-khan.github.io/starter-site/" target="_blank">View</a>
                            </div>
                        </div>
                    </div>

                    <div class="">
                        <div class="card-header" id="headingFour">
                            <h2 class="mb-0">
                                <button class="btn btn-link btn-block text-left collapsed proj-btn" type="button" data-toggle="collapse" data-target="#collapseFour" aria-expanded="false" aria-controls="collapseThree">
                                    Todo List
                                </button><hr />
                                <div id="hover-content">
                                    <img id="proj-img" src={todolist} className="img-fluid" alt="tech-automation" />
                                </div>
                            </h2>
                        </div>
                        <div id="collapseFour" class="collapse" aria-labelledby="headingFour" data-parent="#accordionExample">
                            <div class="card-body">
                                <p>A smart daily planner. A todo list application for managing tasks, that you want to do.</p>
                                <a href="https://waris-khan.github.io/todo-list/" target="_blank">View</a>
                            </div>
                        </div>
                    </div>

                    <div class="">
                        <div class="card-header" id="headingFive">
                            <h2 class="mb-0">
                                <button class="btn btn-link btn-block text-left collapsed proj-btn" type="button" data-toggle="collapse" data-target="#collapseFive" aria-expanded="false" aria-controls="collapseThree">
                                    Khaleej Ships
                                </button><hr />
                                <div id="hover-content">
                                    <img id="proj-img" src={khaleejships} className="img-fluid" alt="tech-automation" />
                                </div>
                            </h2>
                        </div>
                        <div id="collapseFive" class="collapse" aria-labelledby="headingFive" data-parent="#accordionExample">
                            <div class="card-body">
                                <p>A demo website for a client named as "Khaleej Ships and Maintenance" which deals in marine industry outfits.</p>
                                <a href="https://waris-khan.github.io/khaleej-ships/" target="_blank">View</a>
                            </div>
                        </div>
                    </div>

                    <div className="sub-nav">
                        <h6 className="next">Next</h6>
                        <ul className="sub-nav-links">
                            <li>
                                <NavLink exact to="/">Home</NavLink>

                            </li>
                            <li>
                                <NavLink exact to="/contact">Contact</NavLink>
                            </li>
                        </ul>
                    </div>
                </div>

            </section>
        </>
    );
}

export default Portfolio;