import React from 'react';
import { NavLink } from 'react-router-dom';
// import { BoxLoading } from 'react-loadingg';

const Contact = () => {

    return (
        <>
            <section id="contact">
                <div className="maxwidth">
                    <h1>Contact</h1>
                    <div className="contact-detail">
                        <p>xyz@gmail.com</p>
                    </div>
                    <div className="form_div m-auto">
                        <form>
                            <div class="form-group">
                                <input type="text" placeholder="Name" class="form-control" id="exampleInputName" aria-describedby="emailHelp" />
                            </div>
                            <div class="form-group">
                                <input type="email" placeholder="Email" class="form-control" id="exampleInputEmail1" />
                            </div>
                            <textarea class="form-control" placeholder="Message" id="exampleFormControlTextarea1" rows="3"></textarea>

                            <button type="submit" class="btn form_btn">Submit</button>
                        </form>
                    </div>

                    <div className="sub-nav">
                        <h6 className="next">Next</h6>
                        <ul className="sub-nav-links">
                            <li>
                                <NavLink exact to="/">Home</NavLink>

                            </li>
                            <li>
                                <NavLink exact to="/portfolio">Portfolio</NavLink>
                            </li>
                        </ul>
                    </div>
                    
                </div>
            </section>
        </>
    );
}

export default Contact;